"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";

interface BookingData {
  pickup: string;
  dropoff: string;
  date: string;
  time: string;
  phone: string;
  vehicleType: string;
}

export default function BookingForm() {
  const [formData, setFormData] = useState<BookingData>({
    pickup: "",
    dropoff: "",
    date: "",
    time: "",
    phone: "",
    vehicleType: ""
  });
  
  const [errors, setErrors] = useState<Partial<BookingData>>({});
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitSuccess, setSubmitSuccess] = useState(false);
  const [submitError, setSubmitError] = useState("");

  const validateForm = (): boolean => {
    const newErrors: Partial<BookingData> = {};
    
    if (!formData.pickup.trim()) {
      newErrors.pickup = "Pickup location is required";
    }
    
    if (!formData.dropoff.trim()) {
      newErrors.dropoff = "Drop-off location is required";
    }
    
    if (!formData.date) {
      newErrors.date = "Date is required";
    }
    
    if (!formData.time) {
      newErrors.time = "Time is required";
    }
    
    if (!formData.phone.trim()) {
      newErrors.phone = "Phone number is required";
    } else if (!/^\+?[\d\s-()]+$/.test(formData.phone)) {
      newErrors.phone = "Please enter a valid phone number";
    }
    
    if (!formData.vehicleType) {
      newErrors.vehicleType = "Please select a vehicle type";
    }
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleInputChange = (field: keyof BookingData, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    // Clear error when user starts typing
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: undefined }));
    }
  };

  const calculateEstimatedFare = (): number => {
    const baseRates = {
      economy: 150,
      standard: 200,
      premium: 300,
      luxury: 500
    };
    
    const rate = baseRates[formData.vehicleType as keyof typeof baseRates] || 150;
    // Simple distance estimation based on location names (mock calculation)
    const estimatedDistance = Math.max(5, Math.random() * 20 + 5);
    return Math.round(rate + (estimatedDistance * 15));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }
    
    setIsSubmitting(true);
    setSubmitError("");
    
    try {
      const response = await fetch("/api/booking", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(formData),
      });
      
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.message || "Failed to submit booking");
      }
      
      const result = await response.json();
      setSubmitSuccess(true);
      
      // Reset form after successful submission
      setFormData({
        pickup: "",
        dropoff: "",
        date: "",
        time: "",
        phone: "",
        vehicleType: ""
      });
      
    } catch (error) {
      setSubmitError(error instanceof Error ? error.message : "An unexpected error occurred");
    } finally {
      setIsSubmitting(false);
    }
  };

  if (submitSuccess) {
    return (
      <Card className="w-full max-w-2xl mx-auto">
        <CardContent className="pt-6">
          <Alert className="border-green-200 bg-green-50">
            <AlertDescription className="text-green-800">
              <div className="text-center py-8">
                <h3 className="text-2xl font-bold text-green-900 mb-4">Booking Confirmed!</h3>
                <p className="text-lg mb-4">
                  Your taxi has been booked successfully. You will receive a confirmation call shortly.
                </p>
                <p className="text-sm text-green-700 mb-6">
                  Booking ID: #CB{Math.random().toString(36).substr(2, 9).toUpperCase()}
                </p>
                <Button 
                  onClick={() => setSubmitSuccess(false)}
                  className="bg-yellow-500 hover:bg-yellow-600 text-black"
                >
                  Book Another Ride
                </Button>
              </div>
            </AlertDescription>
          </Alert>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card className="w-full max-w-2xl mx-auto">
      <CardHeader>
        <CardTitle className="text-2xl text-center">Book Your Ride</CardTitle>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-6">
          {submitError && (
            <Alert className="border-red-200 bg-red-50">
              <AlertDescription className="text-red-800">
                {submitError}
              </AlertDescription>
            </Alert>
          )}
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="pickup">Pickup Location *</Label>
              <Input
                id="pickup"
                type="text"
                placeholder="Enter pickup location"
                value={formData.pickup}
                onChange={(e) => handleInputChange("pickup", e.target.value)}
                className={errors.pickup ? "border-red-500" : ""}
              />
              {errors.pickup && (
                <p className="text-sm text-red-600">{errors.pickup}</p>
              )}
            </div>
            
            <div className="space-y-2">
              <Label htmlFor="dropoff">Drop-off Location *</Label>
              <Input
                id="dropoff"
                type="text"
                placeholder="Enter drop-off location"
                value={formData.dropoff}
                onChange={(e) => handleInputChange("dropoff", e.target.value)}
                className={errors.dropoff ? "border-red-500" : ""}
              />
              {errors.dropoff && (
                <p className="text-sm text-red-600">{errors.dropoff}</p>
              )}
            </div>
          </div>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="date">Date *</Label>
              <Input
                id="date"
                type="date"
                value={formData.date}
                onChange={(e) => handleInputChange("date", e.target.value)}
                className={errors.date ? "border-red-500" : ""}
                min={new Date().toISOString().split('T')[0]}
              />
              {errors.date && (
                <p className="text-sm text-red-600">{errors.date}</p>
              )}
            </div>
            
            <div className="space-y-2">
              <Label htmlFor="time">Time *</Label>
              <Input
                id="time"
                type="time"
                value={formData.time}
                onChange={(e) => handleInputChange("time", e.target.value)}
                className={errors.time ? "border-red-500" : ""}
              />
              {errors.time && (
                <p className="text-sm text-red-600">{errors.time}</p>
              )}
            </div>
          </div>
          
          <div className="space-y-2">
            <Label htmlFor="vehicleType">Vehicle Type *</Label>
            <Select value={formData.vehicleType} onValueChange={(value) => handleInputChange("vehicleType", value)}>
              <SelectTrigger className={errors.vehicleType ? "border-red-500" : ""}>
                <SelectValue placeholder="Select vehicle type" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="economy">Economy - Rs. 150 base fare</SelectItem>
                <SelectItem value="standard">Standard - Rs. 200 base fare</SelectItem>
                <SelectItem value="premium">Premium - Rs. 300 base fare</SelectItem>
                <SelectItem value="luxury">Luxury - Rs. 500 base fare</SelectItem>
              </SelectContent>
            </Select>
            {errors.vehicleType && (
              <p className="text-sm text-red-600">{errors.vehicleType}</p>
            )}
          </div>
          
          <div className="space-y-2">
            <Label htmlFor="phone">Phone Number *</Label>
            <Input
              id="phone"
              type="tel"
              placeholder="+92-XXX-XXXXXXX"
              value={formData.phone}
              onChange={(e) => handleInputChange("phone", e.target.value)}
              className={errors.phone ? "border-red-500" : ""}
            />
            {errors.phone && (
              <p className="text-sm text-red-600">{errors.phone}</p>
            )}
          </div>
          
          {formData.vehicleType && formData.pickup && formData.dropoff && (
            <div className="bg-yellow-50 p-4 rounded-lg border border-yellow-200">
              <h4 className="font-semibold text-yellow-800 mb-2">Estimated Fare</h4>
              <p className="text-2xl font-bold text-yellow-900">
                Rs. {calculateEstimatedFare()}
              </p>
              <p className="text-sm text-yellow-700 mt-1">
                *Final fare may vary based on actual distance and traffic conditions
              </p>
            </div>
          )}
          
          <Button 
            type="submit" 
            className="w-full bg-yellow-500 hover:bg-yellow-600 text-black font-semibold py-3"
            disabled={isSubmitting}
          >
            {isSubmitting ? "Booking..." : "Book Now"}
          </Button>
        </form>
      </CardContent>
    </Card>
  );
}
